
  .mobile__block {
    z-index: 50;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    // background-color: aqua;
  }
  .mobile__container {
    
    align-items: center;
    // position: relative;
    display: flex;
    justify-content: space-between;
  
    @media screen and (min-width: 768px) {
    
    }
  }

  .navigation {
  
  }
  
  .nav__link {
   
    position: relative;
   
  
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    
    color: var(--primary-text-color);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      color: var(--accent-text-color);
    }
    &:hover::after,
    &:focus::after {
      opacity: 1;
    }
  
    @media screen and (min-width: 768px) {
      display: block;
      // padding-top: 32px;
      padding-bottom: 32px;
      
     
    }
  }
  
  .nav__item:not(:last-child) {
    @media screen and (max-width: 767px) {
      flex-direction: column;
      margin-bottom: 23px;
      border-bottom: 1px solid #E18298;
    }
  }
  

  
  
  .mobile__menu {
    position: absolute;
    top: 0;
    right: 0;
    @media screen and (max-width: 767px) {
      // z-index: 5;
      overflow: auto;
     
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 100%;
      height: 100%;
      padding-left: 20px;
      padding-top: 60px;
      // padding: 48px 40px;
      background: #ffffff;
     
      transform: translateX(100%);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  
    @media screen and (min-width: 768px) {
// z-index: 7;
    
      transform: translateX(100%);
      transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }
  }
  
  .mobile__menu.is-open {
  
    transform: translateX(0);
    background-color: #E17992;
  }

  .button__open    {
    position: absolute;
    top: 27px;
    right: 20px;
  
    background-color: transparent;
    border: none;
  
    &:hover,
    &:focus
   {
      fill: var(--btn-accent-bg-color);
    }
    @media screen and (min-width: 768px) {
    right: 224px;
    }
  }



  .button__close    {
    position: absolute;
    top: 20px;
    right: 20px;
  
    background-color: transparent;
    border: none;
  
    &:hover,
    &:focus
   {
      fill: var(--btn-accent-bg-color);
    }
    @media screen and (min-width: 768px) {
    
    }
  }

  .button__icon {
    margin-left: 16px;
  }

 


  
