.hero-header-wrap {
    background-color: var(--main-bg-color);
    z-index: -1;
}
.hero-header {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    height: 502px;
    

    padding-top: 8px;
    padding-right: 20px;
    padding-left: 20px;
    overflow: hidden;

    @include media(480px) {
        width: 480px;
        height: 502px;
    }
        
    

    @include media(768px) {
        width: 768px;
        height: 416px;
        padding-top: 32px;
        padding-right: 32px;
        padding-left: 32px;


    }

    @include media(1280px) {
        width: 1280px;
        height: 664px;
        padding-top: 40px;
        padding-right: 16px;
        padding-left: 16px;
    }
}

.nav {
    display: flex;
    align-items: center;
} 

.logo {
    text-decoration: none;
}

.nav__menu {
    display: flex;
    align-items: center;
    margin-left: 175px;
    margin-right: auto;
    z-index: 1;
    
   
    @media screen and (max-width: 1279px) {
        display: none;
}
}

.nav__menu-item:not(:last-child) {
   margin-right: 35px;
}

.nav__menu-link {
    text-decoration: none;
    font-family: 'DM Sans';
    font-style: normal;
    @include font (700, 16px, 21px, null);
    color: var(--primary-text-color);
}

.nav__menu-link--current {
    color: var(--accent-text-color);
}



.hero-advantages {
    position: absolute;
    z-index: 1;
    @media screen and (max-width: 767px) {
        display: none;
}
    @include media(768px) {
        top: 94px;
        right: 42px;
    }


    @include media(1280px) {
        top: 157px;
        right: 34px;
    }
}

.hero-advantages__item:first-child {
padding-bottom: 17px;
}

.hero-advantages__numbers {
    font-family: 'Titan One';
    font-style: normal;
    text-align: right;
    text-transform: uppercase;
    color: var(--accent-text-color);


    @include media(768px) {
        @include font (400, 28px, 32px, 0.04em);
        padding-bottom: 2px;
    }
    
    @include media(1280px) {
        @include font (400, 46px, 53px, 0.04em); 
    }

}

.hero-advantages__text {
    font-family: 'DM Sans';
    text-align: right;
    color: var(--primary-text-color);


    @include media(768px) {
        @include font (400, 8px, 10px, null);
    }
    
    @include media(1280px) {
        @include font (400, 14px, 18px, null); 
    }
}


.btn__header {
    @media screen and (max-width: 767px) {
        display: none;
}
}
     

.hero-title {
    position: absolute;
    z-index: 2;
    font-family: 'Titan One';
    @include font(400, 26px, 30px, 0.04em);
    text-transform: uppercase;
    color: var(--primary-text-color);

    width: 218px;
    height: 106px;
    top: 194px;
    left: 20px;

    @include media(768px) {
        @include font(400, 22px, 25px, 0.04em);
        top: 102px;
        left: 32px;
        width: 162px;
        height: 80px;
    }

    @include media(1280px) {
        @include font(400, 38px, 44px, 0.04em);
        top: 147px;
        left: 16px;
        width: 277px;
        height: 137px;
    }
  
}

.hero-title__current {
    font-family: 'Titan One';
    @include font(400, 48px, 46px, null);
    text-transform: uppercase;
    color: var(--accent-text-color);

    @include media(768px) {
        @include font(400, 34px, 31px, 0.04em);
    }
    @include media(1280px) {
        @include font(400, 58px, 52px, 0.04em);
    }
}

.hero__slogan {
    position: absolute;
    z-index: 1;
    bottom: 81px;
    left: 184px;

    width: 114px;
    height: 48px;
    color: #FFFFFF;
    font-family: 'DM Sans';
    @include font(400, 8px, 12px, null);
    
    @media screen and (max-width: 767px) {
        display: none;
}
    @include media(1280px) {
        bottom: 130px;
        left: 229px;

        width: 200px;
        height: 78px;
        @include font(400, 14px, 20px, null);
    }
}

