.hero-primary-btn {
    @include font (400, 8px, 10px, null); 
    @include btn-color(#FFFFFF, #D41443,#C7133E);
    @include btn-form(62.4px, 24px, 20px);
    
    margin-right: 8px;

    @include media(1280px) {
        @include font (400, 14px, 18px, null); 
        @include btn-form(107px, 40px, 20px);

        margin-right: 12px; 
    }

}

.hero-secondary-btn {
    @include font (400, 8px, 10px, null); 
    @include btn-color(#D41443,#FFFFFF,#ECEBEB);
    @include btn-form(82.8px, 24px, 20px);

    @include media(1280px) {
        @include font (400, 14px, 18px, null); 
        @include btn-form(138px, 40px, 20px);
    }
}

